import React from "react";
import {Form, Button, Row, Col, Container} from "react-bootstrap";
import {Redirect} from "react-router-dom";
import {loginRepository} from "./App";
import API from "./API";

class SignUp extends React.Component{
    constructor(props) {
        super(props);
        this.name = "";
        this.username = "";
        this.password = "";
        this.state = {"redirect_to_reserve": false};
    }

    signup() {
        API.signup(this.name, this.username, this.password);
        loginRepository.updateData({username: this.username, password: this.password});
        this.setState({"redirect_to_reserve": true});
    }

    render() {
        if(this.state.redirect_to_reserve)
            return <Redirect to='/reserve'/>;

        return (
            <Container>
                <Row>
                    <Col>
                        <Form className={"text-white"} style={{"backgroundColor": '#282c34', "marginTop": '60px'}}>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label>Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter name" onChange={(event)=>this.name = event.target.value} required/>
                            </Form.Group>

                            <Form.Group controlId="formBasicEmail">
                                <Form.Label>Username</Form.Label>
                                <Form.Control type="username" placeholder="Enter username" onChange={(event)=>this.username = event.target.value} required/>
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Password" onChange={(event)=>this.password = event.target.value} required/>
                            </Form.Group>

                            <Button variant="primary" onClick={() => this.signup()}>
                                Sign Up
                            </Button>

                        </Form>
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default SignUp;
