import React from "react";
import {Form, Button, Row, Col, Container} from "react-bootstrap";
import {Redirect} from "react-router-dom";
import {loginRepository} from "./App";
import API from "./API";
import {toast} from "react-toastify";

class Login extends React.Component{
    constructor(props) {
        super(props);
        this.name = "";
        this.username = "";
        this.password = "";
        this.state = {"redirect_to_reserve": false, 'redirect_to_signup': false};
    }

    login() {
        API.login(this.name, this.username, this.password,()=>{
                loginRepository.updateData({username: this.username, password: this.password});
                this.setState({"redirect": true});
            },
            ()=>{
                toast.error("Wrong username password")
            }
        )
    }

    goToSignup() {
        this.setState({'redirect_to_signup': true});
    }

    render() {
        if(this.state.redirect)
            return <Redirect to='/reserve'/>;
        if(this.state.redirect_to_signup)
            return <Redirect to='/signup'/>;
        return (
            <Container>
                <Row>
                    <Col>
                        <Form className={"text-white"} style={{"backgroundColor": '#282c34', "marginTop": '60px'}}>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label>Username</Form.Label>
                                <Form.Control type="username" placeholder="Enter username" onChange={(event)=>this.username = event.target.value} required/>
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Password" onChange={(event)=>this.password = event.target.value} required/>
                            </Form.Group>

                            <Button variant="primary" onClick={() => this.login()}>
                                Login
                            </Button>

                            <Button variant="secondary" onClick={() => this.goToSignup()}>
                                Sign Up
                            </Button>
                        </Form>
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default Login;
