import axios from "axios";

class API{
    static __defaultThenMethod = () => {};
    static __defaultCatchMethod = () => {};
    static __defaultFinallyMethod = () => {};

    static getStatus(__then = API.__defaultThenMethod, __catch = API.__defaultCatchMethod, __finally = API.__defaultFinallyMethod) {
        const params = `password=dorosteaghaye`;
        axios.get(process.env.REACT_APP_BACKEND_URL + `get_status2?${params}`).then(__then).catch(__catch).then(__finally);
    }

    static setStatus(user, address, value, __then = API.__defaultThenMethod, __catch = API.__defaultCatchMethod, __finally = API.__defaultFinallyMethod) {
        const params = `password=dorosteaghaye&action=reserve&address=${address}&user=${user}&value=${value}`;
        axios.post(process.env.REACT_APP_BACKEND_URL + `parking_action?${params}`).then(__then).catch(__catch).then(__finally);
    }

    static signup(name, username, password, __then = API.__defaultThenMethod, __catch = API.__defaultCatchMethod, __finally = API.__defaultFinallyMethod) {
        const params = `password=dorosteaghaye&name=${name}&user_name=${username}&password_hashed=${password}`;
        axios.post(process.env.REACT_APP_BACKEND_URL + `addUser?${params}`).then(__then).catch(__catch).then(__finally);
    }

    static login(name, username, password, __then = API.__defaultThenMethod, __catch = API.__defaultCatchMethod, __finally = API.__defaultFinallyMethod) {
        const params = `password=dorosteaghaye&name=${name}&user_name=${username}&password_hashed=${password}`;
        axios.post(process.env.REACT_APP_BACKEND_URL + `login?${params}`).then(__then).catch(__catch).then(__finally);
    }
}

export default API;
