let uid = 0;

function getUid(prefix = 'id') {
    return `${prefix}${++uid}`;
}

class BaseRepository {
    constructor() {
        this.data = "";
        this.updateSignals = {};
        this.dataSet = false;
        this.needData = false;
    }

    getDataFromServer() {
    }

    dataCheckInterval() {
        if(!this.dataSet) {
            this.getDataFromServer();
        } else {
            clearInterval(this.timerId);
        }
    }

    addUpdateSignal(event) {
        const sigId = getUid();
        this.updateSignals[sigId] = event;

        if (!this.needData) {
            this.needData = true;
            this.getDataFromServer();
            this.timerId = setInterval(() => this.dataCheckInterval(), 5000);
        }

        if(this.dataSet)
            this.__signal(event);

        return sigId;
    }

    removeUpdateSignal(sigId) {
        if (typeof this.updateSignals[sigId] !== "undefined")
            delete this.updateSignals[sigId];
    }

    __signal(event) {
        event(this.data);
    }

    signalAll() {
        for (const [, event] of Object.entries(this.updateSignals)) {
            this.__signal(event);
        }
    }

    forceUpdateAll() {
        this.getDataFromServer();
    }

    updateData(newData) {
        this.data = newData;
        this.dataSet = true;
        this.signalAll();
    }

    getData() {
        return this.data;
    }
}

export default BaseRepository;