import React from "react";
import {MoonLoader} from "react-spinners";
import {Container, Col, Row} from "react-bootstrap";
import p00 from './00.png';
import p01 from './01.png';
import p11 from './11.png';
import p10 from './10.png';
import {loginRepository, statusRepository} from "./App";
import API from "./API";
import {Redirect} from "react-router-dom";
import {toast} from "react-toastify";


let uid = 0;

function getUid(prefix = 'id') {
    return `${prefix}${++uid}`;
}

class Reserve extends React.Component{
    constructor(props) {
        super(props);
        this.state = {data:"", loading:true, redirect:false};
        this.p = {'00': p00, '01': p01, '10': p10, '11':p11, '22': p00, '21': p01, '12': p10, '02': p00, '20': p00}
    }

    componentDidMount() {
        this.sigid = statusRepository.addUpdateSignal((val)=>{this.setState(
            {data:val, loading:false})});
        this.user = loginRepository.getData();
        if (this.user === "")
            this.setState({redirect:true});

        setInterval(() => statusRepository.forceUpdateAll(), 2000);
    }

    componentWillUnmount() {
        statusRepository.removeUpdateSignal(this.sigid);
    }

    buttonEvent(inp, state) {
        let newValue = state === 0? 1 : 0;
        API.setStatus(this.user.username, inp, newValue, (resp)=>{toast.dark(resp.data)}, ()=>{toast.error("You can't do that")});
        setTimeout(function() {
            statusRepository.forceUpdateAll();
        }, 500);
    }

    getButton(state, pos) {
        if (state === 1)
            return (
                <div key={getUid()} className="col-3 align-middle text-center m-2 no-select">
                </div>
            );
        return (
            <div key={getUid()} onClick={()=>this.buttonEvent(pos, state)} style={{'backgroundColor': 'rgba(255,225,0,0.5)', 'cursor': 'pointer'}} className="col-3 align-middle text-center m-2 no-select display-3">
                {state === 0? "رزرو": "لغو"}
            </div>
        );
    }

    render() {
        if (this.state.redirect)
            return <Redirect to='/login'/>;
        return (
            <div className="d-flex justify-content-center mt-5 w-100">
                {this.state.loading?
                    <MoonLoader color="yellow"/>
                    :
                    <>
                        <Container fluid>
                            <Row>
                                <img className={"w-100 h-100"} src={this.p[this.state.data]}/>
                            </Row>
                            <Row style={{'marginTop': '-14rem', 'height': '12rem', 'lineHeight': '12rem'}} className="justify-content-center">
                                {this.getButton(Math.floor(this.state.data / 10), 1)}
                                {this.getButton(Math.floor(this.state.data % 10), 2)}
                            </Row>
                        </Container>
                    </>
                }
            </div>
        );
    }
}

export default Reserve;
